import React from 'react';
import { Pagination as MuiPagination, Select, MenuItem, Box, Typography } from '@mui/material';

interface PaginationProps {
  pageNumber: number;
  pageSize: number;
  totalCount: number;
  onPageChange: (page: number) => void;
  onPageSizeChange: (pageSize: number) => void;
}

const Pagination: React.FC<PaginationProps> = ({
  pageNumber, pageSize, totalCount, onPageChange, onPageSizeChange
}) => {
  const pageCount = Math.ceil(totalCount / pageSize) || 1;

  return (
    <Box display="flex" alignItems="center" justifyContent="space-between" mt={2}
      sx={{ backgroundColor: '#f6f6f6', padding: 2, borderRadius: 1 }}
    >
      <Select
        value={pageSize}
        onChange={e => onPageSizeChange(Number(e.target.value))}
        size="small"
        style={{ marginLeft: 16 }}
      >
        {[10, 20, 50].map(size => (
          <MenuItem key={size} value={size}>
            <Typography variant="body2">
              {size}개씩 보기
            </Typography>
          </MenuItem>
        ))}
      </Select>
      <MuiPagination
        count={pageCount}
        page={pageNumber}
        onChange={(_, value) => onPageChange(value)}
        color="primary"
      />
      <Typography variant="body1" component="span" style={{ marginRight: 16, fontWeight: 600 }}>
        총 {totalCount}건
      </Typography>
    </Box>
  );
};

export default Pagination; 